'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { createClient } from '@/lib/supabase/client'
import { ThemeToggleSimple } from '@/components/ui/theme-toggle'
import { Bell, Menu, LogOut, User, ChevronDown, Settings, School } from 'lucide-react'
import { toast } from 'sonner'
import Image from 'next/image'

export function DashboardHeader() {
  const [user, setUser] = useState<any>(null)
  const [school, setSchool] = useState<any>(null)
  const [showDropdown, setShowDropdown] = useState(false)
  const [loading, setLoading] = useState(true)
  const router = useRouter()
  const supabase = createClient()

  useEffect(() => {
    const getUser = async () => {
      try {
        const { data: { user } } = await supabase.auth.getUser()
        if (user) {
          setUser(user)
          
          // Fetch school admin data via API
          const response = await fetch('/api/dashboard/school-admin')
          const data = await response.json()
          
          if (response.ok && data.schoolAdmin) {
            setSchool(data.schoolAdmin.schools)
          }
        }
      } catch (error) {
        console.error('Error fetching user data:', error)
      } finally {
        setLoading(false)
      }
    }
    
    getUser()
  }, [supabase])

  const handleLogout = async () => {
    await supabase.auth.signOut()
    toast.success('Logged out successfully')
    router.push('/auth/login')
  }

  return (
    <header className="glass border-b border-border px-6 py-4 sticky top-0 z-40">
      <div className="flex items-center justify-between">
        <div className="flex items-center">
          <Button variant="ghost" size="icon" className="md:hidden mr-2 glass-button">
            <Menu className="h-5 w-5" />
          </Button>
          <div>
            <div className="flex items-center gap-2">
              <div className="w-8 h-8 rounded-lg flex items-center justify-center md:hidden overflow-hidden">
                <Image src="/logo.svg" alt="Logo" width={32} height={32} />
              </div>
              <h1 className="text-xl md:text-2xl font-bold font-heading">
                {loading ? (
                  <span className="inline-block w-32 h-6 bg-slate-200 rounded animate-pulse" />
                ) : (
                  school?.name || 'Dashboard'
                )}
              </h1>
            </div>
            <p className="text-sm text-muted-foreground hidden sm:block">
              Welcome back, {user?.user_metadata?.full_name || user?.email || 'Admin'}
            </p>
          </div>
        </div>

        <div className="flex items-center space-x-2 md:space-x-4">
          <div className="hidden md:block">
            <ThemeToggleSimple />
          </div>

          <Button variant="ghost" size="icon" className="glass-button relative">
            <Bell className="h-5 w-5" />
            <span className="absolute -top-1 -right-1 w-2 h-2 bg-destructive rounded-full animate-pulse" />
          </Button>
          
          <div className="relative">
            <button
              onClick={() => setShowDropdown(!showDropdown)}
              className="flex items-center space-x-2 glass-button px-3 py-2 rounded-xl transition-all duration-200 hover:scale-[1.02]"
            >
              <div className="w-8 h-8 bg-gradient-to-br from-primary to-secondary rounded-lg flex items-center justify-center">
                <User className="h-4 w-4 text-white" />
              </div>
              <div className="hidden sm:block text-left">
                <p className="text-sm font-medium">
                  {user?.user_metadata?.full_name || 'Admin'}
                </p>
                <p className="text-xs text-muted-foreground truncate max-w-[120px]">
                  {user?.email}
                </p>
              </div>
              <ChevronDown className={`h-4 w-4 text-muted-foreground transition-transform duration-200 ${showDropdown ? 'rotate-180' : ''}`} />
            </button>

            {showDropdown && (
              <>
                <div className="fixed inset-0 z-40" onClick={() => setShowDropdown(false)} />
                <div className="absolute right-0 mt-2 w-56 glass-card p-2 z-50 animate-scale-in">
                  <div className="px-3 py-2 border-b border-border mb-2">
                    <p className="text-sm font-medium">{user?.user_metadata?.full_name || 'Admin'}</p>
                    <p className="text-xs text-muted-foreground">{user?.email}</p>
                  </div>
                  
                  <button
                    onClick={() => {
                      setShowDropdown(false)
                      router.push('/dashboard/settings')
                    }}
                    className="w-full flex items-center gap-3 px-3 py-2 rounded-lg text-sm text-muted-foreground hover:text-foreground hover:bg-muted transition-all duration-200"
                  >
                    <Settings className="h-4 w-4" />
                    Settings
                  </button>
                  
                  <button
                    onClick={() => {
                      setShowDropdown(false)
                      handleLogout()
                    }}
                    className="w-full flex items-center gap-3 px-3 py-2 rounded-lg text-sm text-destructive hover:bg-destructive/10 transition-all duration-200"
                  >
                    <LogOut className="h-4 w-4" />
                    Sign Out
                  </button>
                </div>
              </>
            )}
          </div>
        </div>
      </div>
    </header>
  )
}
