'use client'

import Link from 'next/link'
import { usePathname } from 'next/navigation'
import { cn } from '@/lib/utils'
import { ThemeToggleSimple } from '@/components/ui/theme-toggle'
import Image from 'next/image'
import { 
  LayoutDashboard, 
  Users, 
  GraduationCap, 
  Calendar, 
  Megaphone, 
  BookOpen, 
  BarChart3,
  Settings,
  School
} from 'lucide-react'

const navigation = [
  { name: 'Dashboard', href: '/dashboard', icon: LayoutDashboard },
  { name: 'Students', href: '/dashboard/students', icon: Users },
  { name: 'Batches', href: '/dashboard/batches', icon: GraduationCap },
  { name: 'Attendance', href: '/dashboard/attendance', icon: Calendar },
  { name: 'Announcements', href: '/dashboard/announcements', icon: Megaphone },
  { name: 'Academic Content', href: '/dashboard/academic', icon: BookOpen },
  { name: 'Reports', href: '/dashboard/reports', icon: BarChart3 },
  { name: 'School Profile', href: '/dashboard/profile', icon: School },
  { name: 'Settings', href: '/dashboard/settings', icon: Settings },
]

export function DashboardSidebar() {
  const pathname = usePathname()

  return (
    <div className="hidden md:flex md:w-64 md:flex-col">
      <div className="flex flex-col flex-grow pt-5 glass border-r border-border overflow-y-auto">
        <div className="flex items-center justify-between flex-shrink-0 px-4">
          <Link href="/dashboard" className="flex items-center group">
            <div className="w-10 h-10 rounded-xl flex items-center justify-center shadow-lg group-hover:scale-105 transition-transform duration-300 overflow-hidden">
              <Image src="/logo.svg" alt="InstituteTool" width={40} height={40} />
            </div>
            <span className="ml-2 text-lg font-bold font-heading gradient-text">
              InstituteTool
            </span>
          </Link>
        </div>
        
        <div className="mt-8 flex-grow flex flex-col">
          <nav className="flex-1 px-3 pb-4 space-y-1">
            {navigation.map((item, index) => {
              const isActive = pathname === item.href
              return (
                <Link
                  key={item.name}
                  href={item.href}
                  className={cn(
                    'group flex items-center px-3 py-2.5 text-sm font-medium rounded-xl transition-all duration-200',
                    isActive
                      ? 'bg-gradient-to-r from-primary to-primary/80 text-primary-foreground shadow-lg'
                      : 'text-muted-foreground hover:bg-muted hover:text-foreground'
                  )}
                  style={{ animationDelay: `${index * 0.05}s` }}
                >
                  <item.icon
                    className={cn(
                      'mr-3 flex-shrink-0 h-5 w-5 transition-transform duration-200 group-hover:scale-110',
                      isActive ? 'text-primary-foreground' : 'text-muted-foreground group-hover:text-foreground'
                    )}
                  />
                  {item.name}
                  {isActive && (
                    <span className="ml-auto w-1.5 h-1.5 bg-primary-foreground rounded-full animate-pulse" />
                  )}
                </Link>
              )
            })}
          </nav>
        </div>

        {/* Theme Toggle at Bottom */}
        <div className="p-4 border-t border-border">
          <div className="flex items-center justify-between glass-card p-3 rounded-xl">
            <span className="text-sm font-medium text-muted-foreground">Theme</span>
            <ThemeToggleSimple />
          </div>
        </div>
      </div>
    </div>
  )
}
